body {
  margin: 0;
  padding: 0;
  width: 100vw;
  position: relative;
  animation: backgroundMove 10s linear infinite;
}

@keyframes backgroundMove {
  0% {
      background: linear-gradient(45deg, #ff6f61, #6b5b95);
      background-size: 200% 200%;
      background-position: 0 0;
  }
  100% {
      background: linear-gradient(45deg, #6b5b95, #ff6f61);
      background-size: 200% 200%;
      background-position: 100% 100%;
  }
}

@keyframes backgroundMoveForm {
  0% {
      background: linear-gradient(45deg, #ccb8b6, #2d1a5c);
      background-size: 200% 200%;
      background-position: 0 0;
  }
  100% {
      background: linear-gradient(45deg, #ffffff, #d02010);
      background-size: 200% 200%;
      background-position: 100% 100%;
  }
}


@keyframes backgroundMoveButton {
  0% {
      background: linear-gradient(45deg, #ccb8b6, #2d1a5c);
      background-size: 120% 120%;
      background-position: 0 0;
  }
  100% {
      background: linear-gradient(45deg, #ffffff, #d02010);
      background-size: 200% 200%;
      background-position: 100% 100%;
  }
}

.button-link {
  display: inline-block;
  padding: 10px 20px;
  background-color: #084269; /* Your desired button color */
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;

  animation: backgroundMoveButton 10s linear infinite;
  /* Add any other button styling you need */
}

.button-link:hover {
  background-color: #0058a2; /* Change the color on hover */
}
